.card {
  margin: 30px;
  transition-duration: 300ms;
}
.main {
  display: flex;
  flex-wrap: wrap;
  background-image: url("https://sollio.coop/sites/default/files/article_images/COVID-19.jpg");
}
.states {
  display: flex;
  flex-wrap: wrap;

  justify-content: center;
}

.cchanges {
  font-size: x-small;
}

.achanges {
  font-size: x-small;
}
.rchanges {
  font-size: x-small;
}
.dchanges {
  font-size: x-small;
}

.card:hover {
  transform: scale(1.02);
  background-image: url("https://sollio.coop/sites/default/files/article_images/COVID-19.jpg");
  color: white;
}
