.table {
  display: flex;
  margin: 5px;
  justify-content: center;
}

.innertable {
  margin-left: 100px;
  font-size: larger;
}

.main {
  display: flex;
  flex-direction: column;
  color: aliceblue;
}

.title {
  display: flex;
  justify-content: center;
  margin-left: 150px;
}

.smain {
  background-color: black;
}
.small {
  font-size: 0.7rem;
  margin-bottom: 20px;
}

i {
  margin: 2px;
  margin-right: 5px;
}

.head {
  padding: 10px;
  font-size: x-large;
}

@media only screen and (max-width: 900px) {
  .table {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-left: -70px;
  }
  .title {
    margin-left: 40px;
  }
  .innertable {
    margin-bottom: 20px;
  }
}
